/** Search form */
.search-form {
  @extend .form-inline;
}

.search-form label {
  @extend .form-group;

  font-weight: normal;
}

.search-form .search-field {
  @extend .form-control;
}

.search-form .search-submit {
  @extend .btn;
  @extend .btn-secondary;
}

.form-control {
  min-width: 300px;
  letter-spacing: 1.86px;

  @include media-breakpoint-up(lg) {
    width: auto;
  }
}



